<template>
    <div>
        <div v-if="personalStatus==='0'">
            <div class="personal-board-verified">
                <el-button
                        type="primary"
                        class="verifiedBtn"
                        round
                        @click.native="startDialog('isShowBaseInfoDialog')">
                    <h1></h1>
                </el-button>
            </div>

        <el-form
                :model="AuthForm"
                ref="AuthForm"
                class="authform"
                label-width="120px">
        <el-dialog
          title=""
          :visible.sync="isShowBaseInfoDialog"
          width="70%"
          >
            <el-form-item label="" prop="name">
                <el-input
                        type="text"
                        v-model="AuthForm.name"
                        placeholder="">
                </el-input>
            </el-form-item>
            <el-form-item label="" prop="user_no">
                <el-input
                        type="text"
                        v-model="AuthForm.user_no"
                        placeholder="">
                </el-input>
            </el-form-item>
            <el-row>
                <el-col :span="10">
                    <el-form-item>
                        <p></p>
                        <el-upload
                                action=""
                                :auto-upload='false'
                                :on-change='changeUploadPic1'
                                :show-file-list='false'
                        >
                            <img v-if="AuthForm.Pic1.url" :src="AuthForm.Pic1.url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                </el-col>
                <el-col :span="10">
                    <el-form-item>
                        <p></p>
                        <el-upload
                                action=""
                                :auto-upload='false'
                                :on-change='changeUploadPic2'
                                :show-file-list='false'>
                            <img v-if="AuthForm.Pic2.url" :src="AuthForm.Pic2.url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button
                        type="primary"
                        round
                        @click.native="closeDialog('isShowBaseInfoDialog')"
                >
                    
                </el-button>
                <el-button
                        type="primary"
                        round
                        @click.native="submit()"
                >
                    
                </el-button>
            </span>
        </el-dialog>
        </el-form>
    </div>
        <div v-else-if="personalStatus==='1'">...</div>
        <div v-else-if="personalStatus==='2'"></div>
    </div>
</template>

<script>
    import img2base64 from '../../../utils/img2base64'
    export default {
        data() {
            return {
                personalStatus: '',
                isShowBaseInfoDialog: false,
                AuthForm: {
                    name: '',
                    user_no: '',
                    Pic1: '',
                    Pic2: '',
                }
            }
        },
        methods: {
            submit() {
                // 
                let AuthParams = {
                    name: this.AuthForm.name,
                    card_no: this.AuthForm.user_no,
                    card_pic_a: img2base64(this.AuthForm.Pic1.url),
                    card_pic_b: img2base64(this.AuthForm.Pic2.url)
                }
                // axios
                        this.$axios.post('/mrbui/bmbucmm1/0010040.do',this.$qs.stringify(AuthParams)).then(res => {
                            if (res.data.gda.msg_cd !== 'MBU00000') {
                                this.$message({
                                type: 'error',
                                message: res.data.gda.msg_cd
                                })
                            } else {
                              this.isShowBaseInfoDialog = false
                              this.query()
                            }
                        })
            },
            nextDialog(now, next) {
                this[now] = false
                this[next] = true
            },
            closeDialog(attr) {
                this[attr] = false
            },
            startDialog(attr) {
                this[attr] = true
            },
            changeUploadPic1(file) {
                this.$nextTick(
                    () => {
                        this.AuthForm.Pic1 = file
                    });
            },
            changeUploadPic2(file) {
                this.$nextTick(
                    () => {
                        this.AuthForm.Pic2 = file
                    });
            },
            query() {
            this.$axios.get('/mrbui/bmbucmm1/0010160.do').then(res => {
                    if (res.data.gda.msg_cd !== 'MBU00000') {
                        this.$message({
                            type: 'error',
                            message: res.data.gda.msg_cd
                        })
                    } else {
                        this.personalStatus = res.data.personal_sts
                    }
                })
            }
        },
        mounted() {
            this.query()
        }
    }
</script>


<style>
    .personal-board-verified {
        border: 1px dashed #d9d9d9;
        height: 200px;
        width: 500px;
        position: absolute;top:25%;left: 50%;margin-top: -100px;margin-left: -250px;

    }
    .verifiedBtn {
        width: 300px;
        height: 100px;
        border-radius: 6px;
        position: absolute;top:25%;left: 50%;margin-left: -150px;

    }
</style>


