<template>

    <div v-if="type === '0'" class="apply-board">
        <div>
            <el-input
                type="text"
                v-model="company_no"
                class="apply-input-area"
                placeholder="" >
            </el-input>

             <el-button
                type="primary"
                class="apply-submit-btn"
                @click.native="submit"
                >
            
            </el-button>
        </div>
    
    <a href="../#/auth/company"></a><a href="#" @click="startDialog()"></a>
    </div>

    <div v-else-if="type === '1'" class="apply-board">
        ......
        <a href="#" @click="cancel()"></a>
    </div>
     <div v-else-if="type === '4'" class="apply-board">
        /,
        <a href="#" @click="commit()"></a>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                type: sessionStorage.getItem('company_sts'),
                isShowDialog: false,
                company_no: sessionStorage.getItem('company_no')
            }
        },
        methods: {
            submit() {
                let ApplyParams = {
                            company_no: this.company_no+'',
                        }
                this.$axios.post('/mrbui/bmbucmm1/0010050.do',this.$qs.stringify(ApplyParams)).then(res => { 
                    if (res.data.gda.msg_cd !== 'MBU00000') {
                        this.$message({
                            type: 'error',
                            message: res.data.gda.msg_cd
                        })
                    } else {
                        this.type = res.data.company_sts
                        sessionStorage.setItem('company_sts', res.data.company_sts)
                    }
                 })
            },
            cancel() {
                this.$axios.post('/mrbui/bmbucmm1/0010060.do').then(res => { 
                    if (res.data.gda.msg_cd !== 'MBU00000') {
                        this.$message({
                            type: 'error',
                            message: res.data.gda.msg_cd
                        })
                    } else {
                        this.type = res.data.company_sts
                        sessionStorage.setItem('company_sts', res.data.company_sts)
                    }
                 })
            },
            commit() {
                this.$axios.post('/mrbui/bmbucmm1/0010060.do').then(res => { 
                    if (res.data.gda.msg_cd !== 'MBU00000') {
                        this.$message({
                            type: 'error',
                            message: res.data.gda.msg_cd
                        })
                    } else {
                        this.type = res.data.company_sts
                        sessionStorage.setItem('company_sts', res.data.company_sts)
                    }
                 })
            }
        }

    }
</script>


<style>

.index-form-dialog {
    width: 500px;
    height: 300px;
}
.apply-submit-btn {
    margin-top: 50px;
    margin-bottom: 50px;
}
.apply-input-area {
    width: 500px;
}
.apply-board {
   margin-left: 50px;

}
</style>


